import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ReportesService } from "./../reportes.service";
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { estacionservicio } from '../../viewmodel/estacionservicio';
import { ZonaTecnicaService } from '../../zonaTecnica/zona-tecnica.service';
import { TipocompoenenteService } from './../../tipocomponente/tipocompoenente.service';
import { ClienteService } from './../../clientes/cliente.service';
import { Componente } from '../../viewmodel/Componente';
import { Observable } from 'rxjs';
import { of } from 'rxjs/observable/of';
import { TypeaheadMatch } from 'ngx-bootstrap/typeahead';
import { mergeMap } from 'rxjs/operators';
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
import "datatables.net-buttons-bs4";
import { Candeactivateguard } from '../../shared/can-deactivate-guard/can-deactivate-guard.service';

@Component({
  selector: 'app-componentesporcliente',
  templateUrl: './componentesporcliente.component.html',
  styleUrls: ['./componentesporcliente.component.css']
})
export class ComponentesporclienteComponent implements OnInit {
  table = null;
  zonas: any;
  tiposcomponente: any;
  dataSource: Observable<any>;
  razon_social: any;
  fk_zonatecnica: any;
  typeaheadLoading: boolean;
  typeaheadNoResults: boolean;
  codigo_cliente: any;
  lista_item: any;
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private reportesService: ReportesService,
    private router: Router,
    private route: ActivatedRoute,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private zonaTecnicaService: ZonaTecnicaService,
    private tipocompoenenteService: TipocompoenenteService,
    private clienteService: ClienteService,
    private candeactivateguard: Candeactivateguard
  ) {
    this.candeactivateguard.setedit(false);
    this.fk_zonatecnica = -1;
    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.razon_social).subscribe((value) => {
        observer.next(value.data)

      })
    });

  }

  ngOnInit() {
    this.spinnerService.show();
    this.cargarZonas();
    this.cargartiposcomponente();
  }
  cargarZonas() {

    this.zonaTecnicaService.get(1).subscribe((value) => {
      this.zonas = value.data;

    });

  }
  cargartiposcomponente() {

    this.tipocompoenenteService.get(1).subscribe((value) => {
      this.tiposcomponente = value.data;
      this.spinnerService.hide();


    });

  }
  changeTypeaheadLoading(e: boolean): void {

    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.codigo_cliente = e.item.codigo_cliente;
  }

  loadtable() {
    let exampleId: any = $('#example23');

    this.table = exampleId.DataTable({
      dom: 'Bfrtip',
      buttons: [
        'copy', 'csv', 'excel', 'pdf', 'print'
      ],
      "ordering": false,
      paging: false,
      oLanguage: {
        "sLengthMenu": "_MENU_ registros por pgina",
        "sInfoFiltered": " - filtrada de _MAX_ registros",
        "sSearch": "Buscar: ",
        "sZeroRecords": "No existen datos diponibles",
        "sInfoEmpty": " ",
        "sInfo": 'Mostrando _END_ de _TOTAL_',
        "oPaginate": {
          "sPrevious": "Anterior",
          "sNext": "Siguiente"
        }
      },
      createdRow:function(row, data, dataIndex){
       
      }
    });
    $('.buttons-copy, .buttons-csv, .buttons-print, .buttons-pdf, .buttons-excel').addClass('btn btn-primary mr-1');
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy()
      this.table = null
    }
    setTimeout(() => this.loadtable(), 0)
  }

  get() {
    this.spinnerService.show();
    var data = { 'cliente': this.codigo_cliente , 'fk_zonatecnica':this.fk_zonatecnica==-1?'':this.fk_zonatecnica};
    
    var values = $.param(data);
    this.reportesService.get(values)
      .subscribe((response) => {
        this.lista_item = response.data;
        this.reInitDatatable();
        this.spinnerService.hide();

      });
  }
}



