import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ComponentesporclienteComponent } from './../reportes/componentesporcliente/componentesporcliente.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';
import { IncidentesComponent } from './incidentes/incidentes.component';
import { ComentariosComponent } from './comentarios/comentarios.component';
import { HorasSemanalesComponent } from './horas-semanales/horas-semanales.component';
import { PlanificaionSemanalComponent } from './planificaion-semanal/planificaion-semanal.component';
import { KpisComponent } from './kpis/kpis.component';
import { PartesComponent } from './partes/partes.component';
import { AuditoriaComponent } from './auditoria/auditoria.component';
import { IndicadoresComponent } from './indicadores/indicadores.component';
import { GarantiasComponent } from './garantias/garantias.component';
import { ConformidadesComponent } from './conformidades/conformidades.component';
const routes: Routes = [{ path: 'componenteporcliente', component: ComponentesporclienteComponent, canDeactivate: [Candeactivateguard]},
{ path: 'incidentes', component: IncidentesComponent, canDeactivate: [Candeactivateguard]},
{ path: 'comentarios', component: ComentariosComponent, canDeactivate: [Candeactivateguard]},
{ path: 'horassemanales', component: HorasSemanalesComponent, canDeactivate: [Candeactivateguard]},
{ path: 'planificacionsemanal', component: PlanificaionSemanalComponent, canDeactivate: [Candeactivateguard]},
{ path: 'kpis', component: KpisComponent, canDeactivate: [Candeactivateguard]},
{ path: 'partes', component: PartesComponent, canDeactivate: [Candeactivateguard]},
{ path: 'auditoria/:id', component: AuditoriaComponent, canDeactivate: [Candeactivateguard]},
{ path: 'indicadores', component: IndicadoresComponent, canDeactivate: [Candeactivateguard]},
{ path: 'garantias', component: GarantiasComponent, canDeactivate: [Candeactivateguard]},
{ path: 'conformidades', component: ConformidadesComponent, canDeactivate: [Candeactivateguard]},];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule] 
})
export class ReportesRoutingModule { }



