import { Component, OnInit } from "@angular/core";
import { ClienteService } from "@app/clientes/cliente.service";
import { EstacionservicioService } from "@app/estacionservicio/estacionservicio.service";
import { ESTADOINCIDENCIAS } from "@app/mocks/estadoincidente";
import { LocalDataService, NotificationService } from "@app/shared";
import { ZonaTecnicaService } from "@app/zonaTecnica/zona-tecnica.service";
import * as moment from 'moment';
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { BsLocaleService, TypeaheadMatch } from "ngx-bootstrap";
import { Observable } from "rxjs";
import { ReportesService } from "../reportes.service";
declare var $;



@Component({
    selector: 'app-garantias',
    templateUrl: './garantias.component.html',
    styleUrls: ['./garantias.component.css']
  })
export class GarantiasComponent implements OnInit {

    results: any[];
    table = null;
    datafilters = {
        estado: '-1', zonatecnica: '-1', filters: '1'
        , categoria: '-1', codigo_cliente: '', estacionservicio: '-1'
        , fechainicio: '', fechafin: '', tecnico: '-1'
    };
    zonastecnicas: any;
    categorias: any;
    dataSource: Observable<any>;
    razon_social: any;
    typeaheadLoading: boolean;
    typeaheadNoResults: boolean;
    codigo_cliente: any;
    estacionservicio: any;
    estadoincidencias = ESTADOINCIDENCIAS;

    constructor(private spinnerService: Ng4LoadingSpinnerService,
        private reportesService: ReportesService,
        private zonaTecnicaService: ZonaTecnicaService,
        private clienteService: ClienteService,
        private localDataService: LocalDataService,
        private estacionservicioService: EstacionservicioService,
        private localeService: BsLocaleService,
        private notificationService: NotificationService) {

        this.localeService.use('es');
        this.dataSource = Observable.create((observer: any) => {
            // Runs on every search
            this.datafilters.codigo_cliente = '';
            this.clienteService.gettoken(this.razon_social).subscribe((value) => {
                observer.next(value.data);
            });
        });

    }

    ngOnInit(): void {
        this.getZonasTecnicas();
        this.get();
        this.cargarEstacionServicio();
    }

    get() {
        let fi = this.datafilters.fechainicio;
        let ff = this.datafilters.fechafin;
        if (this.datafilters.fechainicio != '' && this.datafilters.fechainicio != null) {
        this.datafilters.fechainicio = moment(this.datafilters.fechainicio).format("DD-MM-YYYY HH:mm:ss");

        }
        if (this.datafilters.fechafin != '' && this.datafilters.fechafin != null) {
        this.datafilters.fechafin = moment(this.datafilters.fechafin).format("DD-MM-YYYY HH:mm:ss");

        }

        this.spinnerService.show();
        this.reportesService.consultar('garantia', this.datafilters).subscribe((response) => {
            this.datafilters.fechainicio = fi;
            this.datafilters.fechafin = ff;
            this.results = response.data;
            this.reInitDatatable();

            this.spinnerService.hide();
            this.datafilters.filters = '';
        },
        (err) => {
          this.spinnerService.hide();
          this.notificationService.onError("Se ha producido un error.");
        });
    }

    private reInitDatatable(): void {
        if (this.table) {
            this.table.destroy()
            this.table = null
        }
        setTimeout(() => this.loadtable(), 0)
    }

    loadtable() {
        let exampleId: any = $('#example23');
    
        this.table = exampleId.DataTable({
            dom: 'Bfrtip',
            buttons: [
            'copy', 'csv', 'excel'
            ],
            "ordering": true,
            paging: true,
            oLanguage: {
            "sLengthMenu": "_MENU_ registros por pgina",
            "sInfoFiltered": " - filtrada de _MAX_ registros",
            "sSearch": "Buscar: ",
            "sZeroRecords": "No existen datos diponibles",
            "sInfoEmpty": " ",
            "sInfo": 'Mostrando _END_ de _TOTAL_',
            "oPaginate": {
                "sPrevious": "Anterior",
                "sNext": "Siguiente"
            }
            },
            createdRow: function (row, data, dataIndex) {
    
            }
        });
    
        $('.buttons-copy, .buttons-csv, .buttons-print, .buttons-pdf, .buttons-excel').addClass('btn btn-primary mr-1');
    }

    getZonasTecnicas() {
        this.zonaTecnicaService.get(1).subscribe((value) => {
          this.zonastecnicas = value.data;
          this.spinnerService.hide();
        });
    }

    changeTypeaheadLoading(e: boolean): void {
        this.typeaheadLoading = e;
    }

    typeaheadOnSelect(e: TypeaheadMatch): void {
        this.datafilters.codigo_cliente = e.item.codigo_cliente;
        this.cargarEstacionServicio();
    }

    cargarEstacionServicio() {
        let empresa = 0;
        this.spinnerService.show();
        empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
        this.estacionservicioService.getactbycliente(empresa, this.datafilters.codigo_cliente).subscribe((value) => {
            this.estacionservicio = value.data;
            this.spinnerService.hide();
        });
    }

    getclassestadoincidencia(estado) {
        let result = this.estadoincidencias.filter(estadoinc => estadoinc.value === estado);
        return result.length > 0 ? result[0].label : '';
    }

    onselectEstacionServicio(estacionserviciosel) {
        this.clienteService.gettoken(estacionserviciosel.razon_social).subscribe((value) => {
            if (value.data.length > 0) {
                this.razon_social = value.data[0].razon_social;
            }
            this.spinnerService.hide();
        });
    }
}



