import { Component, OnInit } from "@angular/core";
import { ClienteService } from "@app/clientes/cliente.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { BsLocaleService } from "ngx-bootstrap";
import { Observable } from "rxjs";
import { ReportesService } from "../reportes.service";
declare var $;



@Component({
    selector: 'app-indicadores',
    templateUrl: './indicadores.component.html',
    styleUrls: ['./indicadores.component.css']
  })
export class IndicadoresComponent implements OnInit {

    dataSource: Observable<any>;
    results: any[];
    headers: any[];
    table = null;
    years = [
        {
            label : '2021',
            value : 2021
        },
        {
            label : '2020',
            value : 2020
        }
    ];
    datafilters = {
        year : ''
    };
    loading = false;

    constructor(private spinnerService: Ng4LoadingSpinnerService,
        private reportesService: ReportesService, 
        private localeService: BsLocaleService,
        private clienteService: ClienteService,) {
        this.localeService.use('es');

    }

    ngOnInit(): void {
        
    }


    get() {
        this.loading = true;
        this.spinnerService.show();
        setInterval(this.checkLoading, 3000);
        this.reportesService.consultar('indicadores', this.datafilters).subscribe((response) => {
            
            this.results = Object.values(response.data);
            let keys = Object.keys(response.data); 
            if (!this.table) {
                this.headers = response.data[keys[0]][0].data;
            }
            //console.log(this.results);
            this.loading = false;
            this.reInitDatatable();
            this.spinnerService.hide();
        })
    }

    checkLoading() {
        while (this.loading) {
            this.spinnerService.show();
        }
    }

    private reInitDatatable(): void {
        if (this.table) {
          this.table.destroy()
          this.table = null
        }
        setTimeout(() => this.loadtable(), 0)
      }
    
    loadtable() {
        let exampleId: any = $('#example23');

        this.table = exampleId.DataTable({
            dom: 'Bfrtip',
            buttons: [
            'copy', 'csv', 'excel'
            ],
            "pageLength" : 9,
            "ordering": true,
            paging: true,
            oLanguage: {
            "sLengthMenu": "_MENU_ registros por pgina",
            "sInfoFiltered": " - filtrada de _MAX_ registros",
            "sSearch": "Buscar: ",
            "sZeroRecords": "No existen datos diponibles",
            "sInfoEmpty": " ",
            "sInfo": 'Mostrando _END_ de _TOTAL_',
            "oPaginate": {
                "sPrevious": "Anterior",
                "sNext": "Siguiente"
            }
            },
            createdRow: function (row, data, dataIndex) {

            }
        });
        exampleId.on( 'draw.dt', this.hideRepetead);
        setTimeout(this.hideRepetead, 1000);

        $('.buttons-copy, .buttons-csv, .buttons-print, .buttons-pdf, .buttons-excel').addClass('btn btn-primary mr-1');
    }

    hideRepetead() {
        let lastIndicator = null;
        let indicador = 0;
        $('tr').find("td.indicator").each(function(key, val){
            let element = $(val);
        
            if (lastIndicator && lastIndicator == element.data('label')){
                indicador++;
                element.hide();
            }
        
            lastIndicator = $(val).data('label');
        });

        //Aumenta el rowspan para generar el agrupador
        $('tr').find("td.indicator").each(function(key, val){
            let element = $(val);
            if (element.is(':visible')){
                element.attr('rowspan', indicador+1);
                return;
            }
        });
    }
}



