import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
declare var $;

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons"; 
import { ZonaTecnicaService } from '../../zonaTecnica/zona-tecnica.service';
import { AuditoriaService } from '../../shared/auditoria/auditoria.service';
import { Router, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-auditoria',
  templateUrl: './auditoria.component.html',
  styleUrls: ['./auditoria.component.css']
})
export class AuditoriaComponent implements OnInit {

  listaitem = [];
  table = null;
  constructor(private spinnerService: Ng4LoadingSpinnerService,
    private zonaTecnicaService: ZonaTecnicaService,
    private auditoriaService: AuditoriaService,
    private notificationService: NotificationService,
    private router: Router,
    private route: ActivatedRoute,) { }

    ngOnInit() {
      this.spinnerService.show();

      this.get();
  
    }
    get() {
      const $id = this.route.snapshot.paramMap.get('id');
      this.auditoriaService.get($id)
        .subscribe((response) => {
          this.listaitem = response.data;
          this.reInitDatatable();
          this.spinnerService.hide();
  
        });
    }
    loadtable() {
      let exampleId: any = $('#example23');    
  
      this.table=exampleId.DataTable({
        dom: 'Bfrtip',
        buttons: [
          'copy', 'csv', 'excel', 'pdf', 'print'
      ],
      oLanguage: {
        "sLengthMenu": "_MENU_ registros por pgina",
        "sInfoFiltered": " - filtrada de _MAX_ registros",
        "sSearch": "Buscar: ",
        "sZeroRecords": "No existen datos diponibles" ,
        "sInfoEmpty": " ",
        "sInfo": 'Mostrando _END_ de _TOTAL_',
        "oPaginate": {
            "sPrevious": "Anterior",
            "sNext": "Siguiente"
        }
    }
      });
    }
    ngAfterViewInit() {
  
     
  
    }
    delete(id) {
      this.spinnerService.show();
      this.zonaTecnicaService.delete(id)
        .subscribe((value) => {
          this.notificationService.onSuccess('ZOna Tecnica Eliminada con Exito.')
          this.get();
          this.spinnerService.hide();
                },(err)=>{
                  this.spinnerService.hide();
                  if(err.status_code==400)
                  {
                   this.notificationService.onError("La zona tecnica no se puede eliminar ya que tiene informacion asociada.");
                  }
                  else{ this.notificationService.onError("Se ha producido un error.");}
                 });
    }
    private reInitDatatable(): void {
      if (this.table) {
        this.table.destroy()
        this.table = null
      }
      setTimeout(() => this.loadtable(), 0)
    }

}



