import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
/*modules */

import { AuthenticationModule } from './../shared';
import { EnvironmentModule } from './../shared';
import { HttpModule } from '@angular/http';
import { FormValidationModule } from './../shared';
import { NotificationModule } from './../shared';
import { LocalDataModule } from './../shared';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { ReportesRoutingModule } from './reportes-routing.module';
import { ComponentesporclienteComponent } from './../reportes/componentesporcliente/componentesporcliente.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';
import { IncidentesComponent } from './incidentes/incidentes.component';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { esLocale } from 'ngx-bootstrap/locale';
import { ComentariosComponent } from './comentarios/comentarios.component';
import { HorasSemanalesComponent } from './horas-semanales/horas-semanales.component';
import { PlanificaionSemanalComponent } from './planificaion-semanal/planificaion-semanal.component';
import { KpisComponent } from './kpis/kpis.component';
import { NgSelectModule } from '@ng-select/ng-select';
import { PartesComponent } from './partes/partes.component';
import { AuditoriaComponent } from './auditoria/auditoria.component';
import { IndicadoresComponent } from './indicadores/indicadores.component';
import { GarantiasComponent } from './garantias/garantias.component';
import { ConformidadesComponent } from './conformidades/conformidades.component';
defineLocale('es', esLocale);
@NgModule({
  imports: [
    CommonModule,
    ReportesRoutingModule,
    FormsModule,
    ReactiveFormsModule,

    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    BsDatepickerModule.forRoot(),
    NgSelectModule,
  ],
  declarations: [
    ComponentesporclienteComponent,
    IncidentesComponent,
    ComentariosComponent,
    HorasSemanalesComponent,
    PlanificaionSemanalComponent,
    KpisComponent,
    PartesComponent,
    AuditoriaComponent,
    IndicadoresComponent,
    GarantiasComponent,
    ConformidadesComponent
  ],
  providers: [Candeactivateguard]
})
export class ReportesModule { }



